
import { defineComponent, computed, PropType } from 'vue';
import { tabHeader } from './interface'
export default defineComponent({
    data() {
        return {
            selectedTitle: '',
            defaultIndex: 0,
        }
    },
    provide() {

        return {
            selectedTitle: computed(() => this.selectedTitle)
        }
    },
    props: {
        header: {
            type: [] as PropType<tabHeader>,
            default: []
        },
        defaultActive: {
            type: String,
        },
        beforeLeave: {
            type: Boolean,
            default: true
        }

    },
    watch: {
        selectedTitle(newValue, Oldvalue) {
            this.$emit('get-tab-value', newValue, Oldvalue)
        }
    },
    created() {
        if (this.defaultActive) {
            this.selectedTitle = this.defaultActive;
        }
        else {
            for (let index = 0; index < Object.values(this.header).length; index++) {
                const element = this.header[index];
                if (element.disable) {
                    console.log('nothing');
                }
                else {
                    this.selectedTitle = element.name;
                    this.defaultIndex = index;
                    break;
                }
            }
        }

    },
    methods: {
        pushTitle(title) {
            if (this.beforeLeave) {
                this.selectedTitle = title
            }
            else {
                this.$emit('before-leave', title)
            }
        },
    }
})
