
import { defineComponent ,ref} from 'vue';
import baseTab from '@/shared/components/Tab/BaseTab.vue';
import baseTabPan from '@/shared/components/Tab/BaseTabPan.vue';
import com210001ss01 from '../com210001ss01/com210001ss01.vue';
import com210001ss02 from '@/views/com/com210001ss02/com210001ss02.vue';
import com210001ss03 from '../com210001ss03/com210001ss03.vue';
import com210001ss04 from '../com210001ss04/com210001ss04.vue';
import { modalController } from '@ionic/vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';

export default defineComponent({
  name: 'com210001sc00',
  components: {
    baseTab,
    baseTabPan,
    com210001ss01,
    com210001ss02,
    com210001ss03,
    com210001ss04,
    BreadCrumb
  },
  data() {
    const tabHeaders = ref([
			{ name: "com210001ss01.id_pw_configuration", disable: false },
			{ name: "com210001ss02.limit_configuration", disable: false },
			{ name: "com210001ss03.ib_policy_configuration", disable: false },
      { name: 'com210001ss04.product_configuration', disable: false }
		]);
    return {
      tabHeaders,
      isEditable: false,
      allowTabChange:true,
      handleSaveOnIDPWConfiguration:false,
      handleSaveOnLimitConfiguration:false,
      handleSaveOnPolicyConfiguration:false,
      operationMode:'normal',
      approvalResponse: [] as any,
      currentTab:'',
      currentTabDisplay:'',
      internetBankingConfigurationButtonEvents:'',
      configurationLimitButtonEvents:'',
      configurationPolicyButtonEvents:'',
      productConfigurationButtonEvents:'',
      tapTitle:''
    }
  },

  
  watch:{
    isEditable(isAllowTabChange) {
      if(isAllowTabChange){
        this.allowTabChange = false;
      }
    },

    operationMode(newValue){
      if(!newValue){
        this.operationMode = 'normal';
      }else{
        this.operationMode = newValue;
      }
      // this.operationMode = newValue
    },

    currentTab(newValue){
      if(newValue == 'com210001ss01'){
        this.currentTabDisplay = 'com210001ss01.id_pw_configuration';
      }
      else if(newValue == 'com210001ss02'){
        this.currentTabDisplay = 'com210001ss02.limit_configuration';
      }
      else if(newValue == 'com210001ss03'){
        this.currentTabDisplay = 'com210001ss03.ib_policy_configuration';
      } else if(newValue == 'com210001ss04'){
        this.currentTabDisplay = 'com210001ss04.product_configuration';
      } else{
        this.currentTabDisplay = newValue;
      }
    }
  },

  computed:{
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
    },

    queryTabId() : string {
			return this.$route.query.tab?.toString() || ''
		},
  },
  
  methods: {
    isAllowTabChange(value){
      this.allowTabChange = value
    },

    handleCurrentTab(newValue): string | void{
      if (this.queryTabId !== '') {
        switch(this.queryTabId){
          case 'COM210001SS01':
            this.currentTab = 'com210001ss01.id_pw_configuration'
            return 'com210001ss01.id_pw_configuration'

          case 'COM210001SS02':
            this.currentTab = 'com210001ss02.limit_configuration'
            return 'com210001ss02.limit_configuration'

          case 'COM210001SS03':
            this.currentTab = 'com210001ss03.ib_policy_configuration'
            return 'com210001ss03.ib_policy_configuration'

          case 'COM210001SS04':
            this.currentTab = 'com210001ss04.product_configuration'
            return 'com210001ss04.product_configuration'
        }
 
      }else{
        this.currentTab = newValue;
      } 

    },

    handleTabChange(isBeingEdited: boolean) {
			this.allowTabChange = !isBeingEdited
		},

    isEditablefromchlide(value){
      this.isEditable = value;
    },

    onClickAction(buttonType:string){
      const getScreenIDFromType = this.currentTab.split('.');
      this.currentTab = getScreenIDFromType[0];

      type componentType = typeof com210001ss01 | typeof com210001ss02 | typeof com210001ss03 | typeof com210001ss04;

      switch(buttonType){
        case 'cancel':
          (this.$refs[this.currentTab] as InstanceType<componentType>).handleEvent(buttonType);
          break;

        case 'edit':
          (this.$refs[this.currentTab] as InstanceType<componentType>).handleEvent(buttonType);
          this.isEditable = true;
          break;
      
        case 'save':
          (this.$refs[this.currentTab] as InstanceType<componentType>).handleEvent(buttonType);
          break;

        default:
          break;
      }
    },
    
    resetActionHandleEvent(newAction) {
      if(newAction.editable == false){
        this.isEditable = true;
      }else{
        this.$dialog.onOpen({
          content: `${this.$t("message.would_you_like_to_save")}`,
          dialogMode:"Question",
          callback: () => {
            this.isEditable = false;
            modalController.dismiss();
          }
        });
      }
    },

    handleOperationMode(mode: string){
      this.operationMode = mode;
    },

    handleApprovalResponse(approvalResponse: any){
      this.approvalResponse = approvalResponse;
    }
  }
})
