
import { defineComponent } from "vue";
import { modalController } from '@ionic/vue';
import { COM21000114_REQ_BODY, COM21000114_RES } from '@/shared/type/COM21000114.type'
import { COM21000124_REQ_BODY, COM21000124_RES } from '@/shared/type/COM21000124.type'
import { COM21000134_REQ_BODY, COM21000134_RES } from '@/shared/type/COM21000134.type'
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";
export default defineComponent({
    name: "com210001ss04",
    components: {

    },
    data() {
        return {
            soborosFixedDepositeProductStatus: [
                { key: 'Y', value: 'ON' },
                { key: 'N', value: 'Off' }
            ],
            productConfiguration: {
                soborosFixedDepositeStatus: '',
                charityInterestRate: ''
            },
            isDisable: true as boolean,
            isInvalidField: false as boolean,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode,
        }
    },
    computed: {
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
        },
    },
    mounted() {
        this.approvalType === '' ? this.getProductConfigurationDetails() : this.requestApprovalMode()
    },
    methods: {
        handleEvent(event: string) {
            switch (event) {
                case 'cancel':
                    this.$dialog.onOpen({
                        dialogMode: "Question",
                        content: `${this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data")}`,
                        callback: () => {
                            this.isDisable = true;
                            this.$emit('isResetEditable', false);
                            this.$emit('isTapChange', true);
                            this.isInvalidField = false;
                            modalController.dismiss();
                            this.getProductConfigurationDetails();
                        }
                    });
                    break;
                case 'edit':    
                    this.isDisable = false;
                    break;
                case 'save':
                    if (this.productConfiguration.soborosFixedDepositeStatus && this.productConfiguration.charityInterestRate) {
                        this.isInvalidField = false;
                        this.$dialog.onOpen({
                            dialogMode: "Question",
                            content: `${this.$t("message.would_you_like_to_save")}`,
                            callback: () => {
                                this.updateProductConfiguration();
                                modalController.dismiss();
                            }
                        });
                    } else {
                        this.isInvalidField = true;
                    }
                    break;
                default:
                    break;
            }
        },

        async getProductConfigurationDetails() {
            const requestBody: COM21000114_REQ_BODY = {
                productCode: '1', // Default Initials
            }
            const responseData = await this.$request.getRequest('COM21000114', requestBody, false) as COM21000114_RES;
            if (responseData.header.result) {
                this.productConfiguration.charityInterestRate = responseData.body.charityRate;
                this.productConfiguration.soborosFixedDepositeStatus = responseData.body.enableYN;
            }
        },

        async registerProductConfiguration() {
            const requestBody: COM21000124_REQ_BODY = {
                productCode: '1', // Default Initials
                enableYN: this.productConfiguration.soborosFixedDepositeStatus,
                charityRate: this.productConfiguration.charityInterestRate
            }
            const responseData = await this.$request.getRequest('COM21000124', requestBody, false) as COM21000124_RES
            if (responseData.header.result) {
                this.$dialog.onOpen({
                    content: `${this.$t("message.register_success")}`,
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                    }
                });
            }
        },

        async updateProductConfiguration() {
            const requestBody: COM21000134_REQ_BODY = {
                productCode: '1', // Default Initials
                enableYN: this.productConfiguration.soborosFixedDepositeStatus,
                charityRate: Number(this.productConfiguration.charityInterestRate)
            }
            const responseData = await this.$request.getRequest('COM21000134', requestBody, false , 'COM210001SS04') as COM21000134_RES
            if (responseData.header.result) {
                this.$dialog.onOpen({
                    content: `${this.$t("message.update_success")}`,
                    dialogMode: "Success",
                    callback: () => {
                        modalController.dismiss();
                    }
                });
                this.isDisable = true;
                this.$emit('isResetEditable', false);
                this.$emit('isTapChange', true);
            }
        },

        async requestApprovalMode() {
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent);
                this.approvalRes = authDetail.body;
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
                this.$emit('operationMode', this.operationMode);
                this.$emit('approvalResponse', this.approvalRes);
                this.productConfiguration.charityInterestRate = authDetailParsed.charityRate;
                this.productConfiguration.soborosFixedDepositeStatus = authDetailParsed.enableYN;
            });
        }
    }
});
