
import { defineComponent } from 'vue';
import { modalController } from '@ionic/vue';
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";
import { COM21000115_REQ_BODY, COM21000115_RES, PolicyConfig } from '@/shared/type/COM21000115.type';
import { COM21000135_REQ_BODY, COM21000135_RES } from '@/shared/type/COM21000135.type';

export default defineComponent({
    name: "com210001ss03",
    data(){
        return{
            isDisable:true,
            inRequiredInput:false,
            approvalRes: {} as any,
			operationMode: 'normal' as screenMode,

            // new
            firstLoginConfig: {} as PolicyConfig,
            autoDeactivateConfig: {} as PolicyConfig,
            pwdMaxErrorCount: {} as PolicyConfig,
            otpMaxErrorCount: {} as PolicyConfig
        }
    },

    async mounted(){
        if(this.approvalType == ''){
            this.getPolicyConfigurationData();
        }else{
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
				let authDetailParsed = JSON.parse(authDetail.body.requestContent);
				this.approvalRes = authDetail.body;

				this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

                this.$emit('operationMode', this.operationMode);
                this.$emit('approvalResponse', this.approvalRes);

				authDetailParsed.body.policyConfig.forEach((config: PolicyConfig) => {
                    switch (config.configCode) {
                        case '003':
                            this.pwdMaxErrorCount = config;
                            break;
                        case '004':
                            this.firstLoginConfig = config;
                            break;
                        case '011':
                            this.autoDeactivateConfig = config;
                            break;
                        case '012':
                            this.otpMaxErrorCount = config;
                            break;
                    }
                })
            });
        }
    },

    computed:{
		approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
        },	
    },

    methods:{
        handleEvent(event: string) {
			switch (event) {
				case 'cancel':
                    this.$dialog.onOpen({
                        dialogMode:"Question",
                        content: `${this.$t("message.are_you_sure_you_want_to_cancel_without_saving_data")}`,
                        callback: () => {
                            this.isDisable = true;
							this.getPolicyConfigurationData();
                            this.$emit('isResetEditable',false);
							this.$emit('isTapChange',true);
                            modalController.dismiss();
                        }
                    });
					break;

				case 'edit':
					this.isDisable = false;
					break;

                case 'save':
                    if(
                        Number(this.firstLoginConfig.configNumber) > 0 &&
                        Number(this.autoDeactivateConfig.configNumber) > 0 &&
                        Number(this.pwdMaxErrorCount.configNumber) > 0 &&
                        Number(this.otpMaxErrorCount.configNumber) > 0
                    ){
                        this.$dialog.onOpen({
                            dialogMode:"Question",
                            content: `${this.$t("message.would_you_like_to_save")}`,
                            callback: async () => {
                                await this.updatePolicyConfiguration().then((result: boolean) => {
                                    if(result){
                                        this.isDisable = true;
                                        this.$emit('isResetEditable',false);
                                        this.$emit('isTapChange',true);
                                    }
                                });
                                modalController.dismiss();
                            }
                        });
                    }
					break;

				default:
					break;
			}
		},

        async getPolicyConfigurationData(){
            const reqBody : COM21000115_REQ_BODY = {
                configCode: "003,004,011,012"
            };

            const responseData = await this.$request.getRequest('COM21000115', reqBody, false) as COM21000115_RES;

            if(responseData.header.result){
                responseData.body.list.forEach((config: PolicyConfig) => {
                    switch (config.configCode) {
                        case '003':
                            this.pwdMaxErrorCount = config;
                            break;
                        case '004':
                            this.firstLoginConfig = config;
                            break;
                        case '011':
                            this.autoDeactivateConfig = config;
                            break;
                        case '012':
                            this.otpMaxErrorCount = config;
                            break;
                    }
                })
            }
        },

        async updatePolicyConfiguration() : Promise<boolean> {
            const reqBody : COM21000135_REQ_BODY = {
                list: [this.firstLoginConfig, this.autoDeactivateConfig, this.pwdMaxErrorCount, this.otpMaxErrorCount]
            };

            const responseData = await this.$request.getRequest('COM21000135', reqBody, false, 'COM210001SS03') as COM21000135_RES;

            if(this.approvalType){
                this.isDisable = true;
            }
   
            if (responseData.header.result) {
                this.getPolicyConfigurationData();
                this.isDisable = true;
                this.$dialog.onOpen({
                    content: `${this.$t("message.update_success")}`,
                    dialogMode:"Success",
                    callback: () => {
                        modalController.dismiss();
                    }
                });
            }

            return responseData.header.result
        }
    },
    
})
